.item
{
  color: #000000;
}

.itemWrapper
{
  padding: 3px 10px 3px;
  border-radius: 2px;
  margin: 3px 0 3px;
}

.itemWrapper:hover
{
  background-color: var(--neutral-color-desaturated);
  cursor: pointer;
}

.itemWrapper:active
{
  background-color: var(--accent-color2);
}

.itemIcon
{
  vertical-align: bottom;

  margin-right: 5px;
}

.hotkeyCaption
{
  margin-left: 5px;
  color: var(--neutral-color);
}
